.noor-block-accordion {
  display: flex;
  align-items: center;
  padding: 0 2rem;
  margin: 1rem auto 0 !important; }
  .noor-block-accordion__title {
    width: 100%;
    margin: 0.5rem 0 !important; }
  .noor-block-accordion__icon[data-state="true"]  {
    display: block; }
  .noor-block-accordion__icon[data-state="false"] {
    display: none; }
  .noor-block-accordion__content {
    height: 0;
    padding: 0 2rem;
    overflow: hidden; }
  .noor-block-accordion[aria-expanded="true"] + .noor-block-accordion__content {
    height: auto; }
